<template>
<div>

<Form :model="formLeft" label-position="left" :label-width="100">
        <FormItem label="食品名称">
            <Input v-model="formLeft.foodName"></Input>
        </FormItem>
        <FormItem label="食品描述">
            <Input v-model="formLeft.foodDescribe"></Input>
        </FormItem>
           <FormItem label="食品价格">
            <Input v-model="formLeft.foodPrice"></Input>
        </FormItem>
        <Button @click="add" type="primary">添加食品</Button>
    </Form>  
</div>
</template>
<script>
import {addFood} from '@/api/api.js'
export default {
    data(){
        return{
            canteenId:this.$route.query.canteenId,
            formLeft:{
                  foodId:'',
                  foodDescribe:'',
                  foodPrice:'',
                  foodName:'',
                  canteenId:this.$route.query.canteenId
                },
        }
    },
    mounted(){
       
    },
    methods:{
        add(){
            addFood(this.formLeft).then(res=>{
                this.$Message.success('添加成功');
                this.$router.push({name:'sellerMain'})
            },err=>{
                
            })
        }
    }
}
</script>
